function sliderAdvantages() {
    var swiper = new Swiper('.advantages .swiper', {
        slidesPerView: '4',
        spaceBetween: 30,
        pagination: {
            el: ".advantages .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 4,
                spaceBetween: 30,
            },
        }
    })
}
function sliderFeedback() {
    var swiper = new Swiper('.feedback .swiper', {
        slidesPerView: 'auto',
        spaceBetween: 65,
        slideToClickedSlide: true,
        loop: true,
        navigation: {
            nextEl: '.feedback .swiper-button-next',
            prevEl: '.feedback .swiper-button-prev',
        },
        breakpoints: {
            320: {
                spaceBetween: 20,
            },
            993: {
                spaceBetween: 65,
            }
        }
    })
}
function sliderArticles() {
    var swiper = new Swiper('.articles .swiper', {
        slidesPerView: '3',
        spaceBetween: 30,
        navigation: {
            nextEl: '.articles .swiper-button-next',
            prevEl: '.articles .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
                spaceBetween: 20,
            },
            577: {
                slidesPerView: '2',
                spaceBetween: 20,
            },
            993: {
                slidesPerView: '3',
                spaceBetween: 30,
            }
        }
    })
}


$(document).ready(function() {

    sliderAdvantages();
    sliderFeedback();
    sliderArticles();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Before - After
    $(".before-after").twentytwenty({
        default_offset_pct: 0.5,
        no_overlay: true,
    });

    // Table-show
    $('.article-table__show').click(function () {
        $(this).remove()
        $('.table-hide').slideToggle()
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Accordion
        $(".questions__item-show").click((function(){
            $(this).toggleClass("questions__item-show--active"),
            $(this).siblings(".questions__item-hidden").slideToggle()}));

        //
        if (window.matchMedia("(max-width: 576px)").matches) {
            $(".footer-top__item-show").click((function(){
            $(this).toggleClass("footer-top__item-show--active"),
            $(this).siblings(".footer-top__item-hidden").slideToggle()}));
        }

    $(".article-table__show").click((function(){
        $(this).toggleClass("article-table__show--active"),
            $(this).siblings(".questions__item-hidden").slideToggle()}));

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    //
    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // DropZone
    var myDropzone = new Dropzone(".my-dropzone", { url: "/file/post",
        uploadMultiple: false,
        maxFiles: 1,
        acceptedFiles:'image/*',
    });

    var myDropzone2 = new Dropzone(".my-dropzone2", { url: "/file/post",
        uploadMultiple: false,
        maxFiles: 1,
        acceptedFiles:'image/*',
    });



})





// Scroll
jQuery(window).scroll(function(){
    var $sections = $('.section');
    $sections.each(function(i,el){
        var top  = $(el).offset().top-100;
        var bottom = top +$(el).height();
        var scroll = $(window).scrollTop();
        var id = $(el).attr('id');
        if( scroll > top && scroll < bottom){
            $('a.active').removeClass('active');
            $('a[href="#'+id+'"]').addClass('active');

        }
    })
});

$(".article__sidebar-menu").on("click","a", function (event) {

    event.preventDefault();

    var id  = $(this).attr('href'),

        top = $(id).offset().top;

    $('body,html').animate({scrollTop: top}, 200);
});


// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}



